@import "src/assets/variables";

.calendar_agendamentos_container {
  .chevron_date {
    color: $grey;
    cursor: pointer;
    @include putTransition();
    &:hover {
      color: $darkGrey; } }
  h1 {
    text-align: center;
    margin: auto;
    padding: 2% 3%; }
  .container_table {
    width: 100%;
    overflow-x: auto; }
  .calendar_table {
    width: 98%;
    border-spacing: 5px;
    table-layout: fixed;
    margin-left: 1%;
    overflow-x: auto;
    text-align: center;
    padding-bottom: 5%;
    thead {
      th {
        padding-bottom: 1.5%; } }
    tbody {
      tr {
        td {
          padding: 2% 0;
          width: 100%; }
        td:not(:first-child) {
          cursor: pointer;
          @include putTransition();
          &:hover:not(.alugado) {
            box-shadow: 0 3px 4px rgba(0,0,0,0.2); } }
        .alugado {
          box-shadow: transparent;
          &:hover {
            box-shadow: transparent; }
          i {
            color: $grey; } } }
      td:first-child {
        color: $grey;
        width: 7.5%; } }
    .free {
      color: $grey;
      box-shadow: 0 1px 2px rgba(0,0,0,0.2);
      background-color: $lightGrey;
      &:hover {
        background-color: darken($lightGrey, 1); }
      &:active {
        transform: translateY(2px);
        box-shadow: 0 .3px 0 rgba(0,0,0,0.1); } }
    .occupied {
      color: #FFFFFF;
      box-shadow: 0 1px 2px rgba(0,0,0,0.2);
      border-radius: 5px;
      background: transparent linear-gradient(180deg, darken(#98EEF0, 5) 0%, #02BAC0 100%) 0% 0% no-repeat padding-box; } } }

@media (max-width: 641px) {
  .calendar_agendamentos_container {
    h1 {
      font-size: 20px;
      padding: 25px 15px; }
    .container_table {
      .calendar_table {
        width: 1000px; } } } }
